import React from 'react'
import { Link } from 'react-router-dom'
import '../Css/Navbar.css'
const Navbar = () => {
    const RenderMenu = () => {
        if (window.localStorage.getItem("payload") === "true") {
            return (
                <nav className="nav">
                    <Link className="a" to="/">Home</Link>
                    <Link className="a" to="/profile">Profile</Link>
                    <Link className="a" to="/drive">CleanUp Drives</Link>
                    <Link className="a" to="/regnew">Register</Link>
                    <Link className="a" to="/logout">Logout</Link>
                </nav>
            )
        } else if (window.localStorage.getItem("payload") === "false") {
            return (
                <nav className="nav">
                    <Link className="a" to="/">Home</Link>
                    <Link className="a" to="/login">Sign In</Link>
                    <Link className="a" to="/register">Sign Up</Link>
                    <a className="a" href="#contactus">Contact Us</a>
                </nav>
            )
        }
    }
    return (
        <div>

            <header className="header">
                <div className="left">
                    {/* <img className="img" src="Logo-Logo.png" alt="Logo"/> */}
                    <h2 className="h2">Hydriwe</h2>
                </div>
                <RenderMenu />
            </header>

        </div>
    )
}

export default Navbar
