import React from 'react'
import "../Css/Drive.css"
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from "react"
import { fromLatLng } from "react-geocode";
const Drive = () => {
    const history = useNavigate()
    const [driveData, setdriveData] = useState({});
    document.addEventListener("DOMContentLoaded", function () {
        document.querySelector('[data-toggle="popover"]').popover();
    });

    const getbuyerdata = async () => {
        try {
            const res = await fetch("/cleandrive", {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                },
                credentials: "include"
            })

            const data = await res.json();
            console.log(data)
            setdriveData(data)

            if (!res.status === 200) {
                const error = new Error(res.error)
                throw error
            }
        } catch (error) {
            history('/seller-login')
            console.log(error)
        }
    }

    useEffect(() => {
        getbuyerdata();
    });
    return (
        <div className='body-4'>
            <div class="parentheader">
                <div class="elements SNO">S No.</div>
                <div class="elements name">Name</div>
                <div class="elements city">City</div>
                <div class="elements startdate">Start Date</div>
                <div class="elements enddate">End Date</div>
                <div class="elements description">Description</div>
                <div class="elements register">Register!</div>
            </div>



            {
                Array.isArray(driveData) && driveData.map(driveDatas => {
                    fromLatLng(driveDatas.lat, driveDatas.long)
                        .then(({ results }) => {
                            const { lat, lng } = results[0].geometry.location;
                            console.log(lat, lng);
                        })
                        .catch(console.error);
                    return (
                        <div class="eachdrive">
                            <div class="element SNO">S No.</div>
                            <div class="element name">{driveDatas.name}</div>
                            <div class="element city">{driveDatas.lat}</div>
                            <div class="element startdate">{driveDatas.startdate}</div>
                            <div class="element enddate">{driveDatas.enddate}</div>
                            <div class="element description">{driveDatas.des}</div>
                            <div class="element enddate">Register! </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Drive
