import React from 'react'
import { useState } from 'react'
import { useNavigate } from "react-router-dom"
import at from "../Images/at.png"
import key from "../Images/key.png"

const Login = () => {
    const history = useNavigate()
    const [user, setUser] = useState({
        email: "",
        password: "",
        user_role: "Farmer"
    });

    let name, value;
    const handleInput = (e) => {
        console.log(e)
        name = e.target.name;
        value = e.target.value;
        setUser({ ...user, [name]: value })
    }


    const postData = async (e) => {
        e.preventDefault();
        const { email, password, user_role } = user
        console.log(user)

        const res = await fetch("/user-signin", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                email, password, user_role
            })
        });
        const data = res.json();
        if (res.status === 400 || res.status === 422 || !data) {
            window.alert("Invalid credentials");
        } else {
            window.localStorage.setItem("payload", "true")
            window.localStorage.setItem("homereload", "true")
            window.alert("Login Successful");
            history('/')
        }
    }
    return (
        <div className='bodysignup'>
            <div className="wapper">
                <div className="form-login">
                    <h2>Login to an Account</h2>
                    <form action="#">
                        <div className="input-box">
                            <label for="Email">Email</label>
                            <img src={at} alt="email" />
                            <br />
                            <input type="email" required onChange={handleInput} name="email" value={user.email} />
                        </div>
                        <div className="input-box">
                            <label for="Password">Password</label>
                            <img src={key} alt="email" />
                            <br />
                            <input type="password" required onChange={handleInput} name="password" value={user.password} />
                        </div>
                        <div className="buttion">
                            <button type="submit" onClick={postData}>Login</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Login
