import React from 'react'
import "../Css/Signup.css"
import at from "../Images/at.png"
import key from "../Images/key.png"
import phonecall from "../Images/phone-call.png"
import location from "../Images/location.png"
import { useState } from 'react'

const Signup = () => {
    const [user, setUser] = useState({
        email: "",
        password: "",
        phonenumber: "",
        city: "",
        role: "User"
    });

    let name, value;
    const handleInput = (e) => {
        console.log(e)
        name = e.target.name;
        value = e.target.value;
        setUser({ ...user, [name]: value })
    }


    const postData = async (e) => {
        e.preventDefault();
        const { email, password, phonenumber, city, role } = user
        console.log(user)

        const res = await fetch("/user-signup", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                email, password, phonenumber, city, role
            })
        });
        const data = await res.json();
        console.log(data)
        console.log(res.status)
        if (res.status === 422 || res.status === 404 || !data) {

            window.alert("Invalid Registration")
            console.log("Invalid Registration")

        } else if (res.status === 500) {
            window.alert("Internal Server Error")
            console.log("Internal Server Error")
        } else {
            window.alert("Registration Successful");
            console.log("Registration Successful");
            window.location.reload();
        };
    }
    return (
        <div className='bodysignup'>
            <div className="wapper">
                <div className="form-login">
                    <h2>Create an Account</h2>
                    <form action="#">
                        <div className="input-box">
                            <label for="Email">Email</label>
                            <img src={at} alt="email" />
                            <br />
                            <input type="email" required onChange={handleInput} name="email" value={user.email} />
                        </div>
                        <div className="input-box">
                            <label for="Password">Password</label>
                            <img src={key} alt="email" />
                            <br />
                            <input type="password" required onChange={handleInput} name="password" value={user.password} />
                        </div>
                        <div className="input-box">
                            <label for="number">Your Phone Number</label>
                            <img src={phonecall} alt="email" />
                            <br />
                            <input type="number" required onChange={handleInput} name="phonenumber" value={user.phonenumber} />
                        </div>
                        <div className="input-box">
                            <label for="text">City</label>
                            <img src={location} alt="email" />
                            <br />
                            <input type="text" required onChange={handleInput} name="city" value={user.city} />
                        </div>
                        <div className="buttion">
                            <button type="submit" onClick={postData}>Sign Up</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Signup
