import React from 'react'
import Logo from "../Images/Main_Logo-removebg-preview.png.jpg"
import "../Css/Register.css"
import { useState } from 'react'

const Register = () => {
    const [user, setUser] = useState({
        firstname: "",
        lastname: "",
        driveid: "",
        email: "",
        age: "",
        gender: "",
        city: "",
        pincode: ""
    });

    let name, value;
    const handleInput = (e) => {
        console.log(e)
        name = e.target.name;
        value = e.target.value;
        setUser({ ...user, [name]: value })
    }


    const postData = async (e) => {
        e.preventDefault();
        const { firstname, lastname, driveid, email, age, gender, city, pincode } = user
        console.log(user)

        const res = await fetch("/registerfordrive", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                firstname, lastname, driveid, email, age, gender, city, pincode
            })
        });
        const data = await res.json();
        console.log(data)
        console.log(res.status)
        if (res.status === 422 || res.status === 404 || !data) {

            window.alert("Invalid Registration")
            console.log("Invalid Registration")

        } else if (res.status === 500) {
            window.alert("Internal Server Error")
            console.log("Internal Server Error")
        } else {
            window.alert("Registration Successful");
            console.log("Registration Successful");
        };
    }
    return (
        <div className="bodysignup-1">
            <div class="img-1">
                <img src={Logo} alt="" className="img-1" />
            </div>
            <div class="wrapper">
                <div class="content">
                    <h2 class="Register">Register To Create an Impact</h2>
                    <div class="table">
                        <div class="panel, rightpanel">
                            <div class="input-box">
                                <label>First Name</label>
                                <br />
                                <input type="text" required placeholder="Enter your First Name" onChange={handleInput} name="firstname" value={user.firstname} />
                            </div>
                            <div class="input-box">
                                <label>Last Name</label>
                                <br />
                                <input type="text" required placeholder="Enter your Lat Name" onChange={handleInput} name="lastname" value={user.lastname} />
                            </div>
                            <div class="input-box">
                                <label>Drive Id get it from Cleanup Drive page</label>
                                <br />
                                <input type="text" required placeholder="Enter your Drive Id from Clean Drives page" onChange={handleInput} name="driveid" value={user.driveid} />
                            </div>
                            <div class="input-box">
                                <label>Email</label>
                                <br />
                                <input type="text" required placeholder="Enter your Email" onChange={handleInput} name="email" value={user.email} />
                            </div>
                        </div>
                        <div class="panel, leftpanel">
                            <div class="input-box">
                                <label>Age</label>
                                <br />
                                <input type="text" required placeholder="Enter your Age" onChange={handleInput} name="age" value={user.age} />
                            </div>
                            <div class="input-box">
                                <label>Gender</label>
                                <br />
                                <input type="text" required placeholder="Enter your Gender" onChange={handleInput} name="gender" value={user.gender} />
                            </div>
                            <div class="input-box">
                                <label>City</label>
                                <br />
                                <input type="text" required placeholder="Enter your city" onChange={handleInput} name="city" value={user.city} />
                            </div>
                            <div class="input-box">
                                <label>Pin Code</label>
                                <br />
                                <input type="text" required placeholder="Enter your Pin Code" onChange={handleInput} name="pincode" value={user.pincode} />
                            </div>
                        </div>
                    </div>
                    <button type="submit" class="button" onClick={postData}>Update Profile</button>
                </div>
            </div>
        </div>
    )
}

export default Register
